/*
* base reset
*/

*,
::after,
::before {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
}

html,
body {
  min-height: 100%;
  font-size: 1rem;
}

body {
  min-width: 320px;
  margin: 0;
  font-family: $font-base;
  color: $black;
  line-height: 1.5;
  word-break: keep-all;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeSpeed;
}

input,
button,
textarea,
select {
  font-family: $font-base;
  color: $black;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  overflow-wrap: break-word;
}

p {
  hyphens: auto;
}

a {
  color: $grey-d4;
  cursor: pointer;
  text-decoration: none;
}

strong {
  font-weight: 600;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

img,
picture,
video,
canvas,
svg {
  max-width: 100%;
  height: auto;
}

svg {
  display: inline-block;
  vertical-align: top;
}

pre {
  white-space: pre-wrap;
  word-wrap: break-word;
}

iframe {
  border: 0;
}

ul,
ol,
dl,
dt,
dd {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

em,
address {
  font-style: normal;
}

em,
mark {
  color: $color-secondary;
  background-color: transparent;
}

hr {
  margin: 1.6rem 0;
  border-color: $color-hint;
}

figure,
form {
  margin: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  vertical-align: top;
  border: 0 none;
}

label {
  cursor: pointer;
}

button {
  box-sizing: border-box;
  padding: 0;
  background: transparent;
  border: 0 none;
  cursor: pointer;
  -webkit-appearance: none;
}

button,
input,
select,
textarea {
  margin: 0;
  font-size: 100%;
  vertical-align: baseline;
  line-height: inherit;
}

label {
  cursor: pointer;
}

[type='checkbox'],
[type='radio'] {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}

input[type='number'],
input[type='text'],
input[type='password'],
input[type='file'],
input[type='tel'],
input[type='email'],
select,
option,
textarea,
input[type='submit'],
button {
  -webkit-appearance: none;
}

input[type='number']:focus,
input[type='text']:focus,
input[type='password']:focus,
input[type='file']:focus,
input[type='tel']:focus,
input[type='email']:focus,
select:focus,
option:focus,
textarea:focus,
input[type='submit']:focus,
button:focus {
  outline: 0;
}

[type='date'],
[type='datetime-local'],
[type='month'],
[type='time'] {
  -webkit-appearance: listbox;
}

button,
[type='button'],
[type='reset'],
[type='submit'],
[type='file'] {
  -webkit-appearance: button;
  border-radius: 0;
}

button:not(:disabled),
[type='button']:not(:disabled),
[type='reset']:not(:disabled),
[type='submit']:not(:disabled),
[type='file']:not(:disabled) {
  cursor: pointer;
}

[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
  -webkit-appearance: none;
}

[type='number']::-webkit-inner-spin-button {
  display: none;
}

[type='search'] {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  border-radius: 0;
  outline-offset: -2px;
  -webkit-appearance: none;
  -webkit-appearance: textfield;
}
[type='search']::-webkit-search-cancel-button,
[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

button::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='submit']::-moz-focus-inner,
[type='file'] > [type='button']::-moz-focus-inner {
  padding: 0;
  border: none;
}

textarea,
[type='text'],
[type='email'],
[type='password'],
[type='submit'],
[type='search'],
[type='tel'],
[type='number'] {
  border-radius: 0;
  -webkit-appearance: none;
}

textarea {
  vertical-align: top;
  resize: vertical;
}

input:invalid,
textarea:invalid {
  background-color: transparent;
}

table {
  width: 100%;
  border-collapse: collapse;
  empty-cells: show;
}

td {
  vertical-align: middle;
}

select {
  vertical-align: middle;
  border-radius: 0;
  -moz-appearance: none;

  &::-ms-expand {
    display: none;
  }
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

summary {
  display: list-item;
  vertical-align: top;
}

[hidden],
hr,
template {
  display: none;
}

::selection {
  color: $white;
  background: $primary;
  text-shadow: none;
}

::-webkit-input-placeholder,
::-ms-input-placeholder,
:-moz-placeholder {
  color: #555;
}

::placeholder {
  text-overflow: ellipsis;
}

:placeholder-shown {
  text-overflow: ellipsis;
}
