@charset "UTF-8";

// utils
@import 'utils/variable';
@import 'utils/mixins';

// base
@import 'base/path';
@import 'base/reset';

.visually-hidden,
.visually-hidden-focusable:not(:focus):not(:focus-within) {
  @include visually-hidden();
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.is-animated {
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transform: translateY(100%);
  transition-property: opacity;
  transition-duration: 1.5s;
  transition-timing-function: cubic-bezier(0.41, 0.01, 0.16, 1.01);
  transition-delay: 0.5s;
}

.section.active {
  .is-animated {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    transition: 1s all ease-in-out 0.1s;
  }
}

.gs__reveal {
  opacity: 0;
  visibility: hidden;
  will-change: transform, opacity;
}

body.loaded {
  .hero__heading {
    > span:nth-child(1) {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      transition: 1s all ease-in-out 0.1s;
    }

    > span:nth-child(2) {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      transition: 1s all ease-in-out 0.25s;
    }

    > span:nth-child(3) {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      transition: 1s all ease-in-out 0.4s;
    }

    > span:nth-child(4) {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      transition: 1s all ease-in-out 0.55s;
    }

    > .feature__text {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      transition: 1s all ease-in-out 0.7s;
    }
  }
}

body {
  &:not(.fp-viewing-page1) .logo {
    background-image: url(../images/logo.svg);
  }

  &.fp-viewing-page1 .fp-tooltip {
    color: rgba(255, 255, 255, 0.3);
  }

  &.fp-viewing-page5 .logo {
    background-image: url(../images/logo_white.svg);
  }

  &.fp-viewing-page5 .fp-tooltip {
    color: rgba(255, 255, 255, 0.3);
  }
  &.fp-viewing-page5 #fp-nav ul li:hover .fp-tooltip,
  &.fp-viewing-page5 #fp-nav.fp-show-active a.active + .fp-tooltip {
    color: #fff;
  }
}

header {
  position: fixed;
  top: 70px;
  right: 0;
  left: 0;
  display: flex;
  width: 100%;
  padding: 0 70px;
  z-index: 100;

  @media screen and (max-width: 1440px) {
    top: 0;
    padding: 4.861vw;
  }
  @media screen and (max-width: 767px) {
    padding: 6.528vw 1rem;
  }

  .logo {
    display: flex;
    align-items: center;
    width: 80px;
    height: 58px;
    background-image: url(../images/logo_white.svg);
    background-repeat: no-repeat;
    background-size: 100%;

    @media screen and (max-width: 1440px) {
      width: 5.556vw;
      height: 4.028vw;
    }

    @media screen and (max-width: 767px) {
      width: 16.667vw;
      height: 12.083vw;
    }
  }

  &.scroll .logo {
    background-image: url(../images/logo.svg);
  }

  nav {
    display: flex;
    margin-left: auto;
    display: none;

    > ul {
      display: flex;

      > li {
        + li {
          margin-left: 1rem;
        }
        a {
          font-size: 1rem;
          text-transform: uppercase;
        }
      }
    }
  }
}

.fp-viewing-page1 {
  .logo {
    svg {
      fill: #fff;
    }
  }
}

.n1 {
  background-image: url(../images/common/img_hero.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
  }

  .fp-tableCell {
    vertical-align: middle;
  }
}

.n3,
.n6 {
  background-color: #f9f9f9;
}

.n5 {
  background-color: #111;

  .section__title,
  .section__subtitle {
    color: #fff;
  }
  strong {
    display: flex;
    margin-top: 0.5rem;
    color: #fff;
  }
}

.hero__heading {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 300px;
  font-size: 80px;
  font-weight: 700;
  line-height: 1.45;
  color: #fff;
  text-transform: uppercase;
  opacity: 1;
  transition-property: opacity;
  transition-duration: 1.5s;
  transition-timing-function: cubic-bezier(0.41, 0.01, 0.16, 1.01);
  transition-delay: 0.15s;

  @media screen and (max-width: 1440px) {
    padding-left: 19.531vw;
    font-size: 5.208vw;
  }
  @media screen and (max-width: 959px) {
    justify-content: center;
    height: 100%;
  }
  @media screen and (max-width: 767px) {
    padding-right: 8vw;
    padding-left: 8vw;
    font-size: 7.917vw;
  }

  .stroke {
    font-family: 'Pretendard';
    color: transparent;
    -webkit-text-stroke: 1px #fff;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
  }

  > span,
  > p {
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transform: translateY(100%);
  }
}

.feature__text {
  font-size: 26px;
  font-weight: 100;
  color: #fff;

  @media screen and (max-width: 1440px) {
    font-size: 1.693vw;
  }
  @media screen and (max-width: 767px) {
    margin-top: 3.125vw;
    font-size: 3.385vw;
  }
}

.section {
  position: relative;

  @media screen and (max-width: 959px) {
    height: 100%;

    &.n1 {
      height: 100vh;
    }
  }
}

.section__wrapper {
  width: 100%;
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 30px;

  @media screen and (max-width: 767px) {
    padding-right: 1rem;
    padding-left: 1rem;
  }
}

.section_inner {
  padding: 70px 0 100px;

  @media screen and (max-width: 1600px) {
    padding-top: 11.181vw;
    padding-bottom: 6.25vw;
  }

  @media screen and (max-width: 767px) {
    padding-top: 21.333vw;
    padding-bottom: 20.833vw;
  }
}

.section__header {
  display: flex;
  align-items: flex-start;

  @media screen and (max-width: 1279px) {
    flex-direction: column;
  }
}

.section__title {
  margin-bottom: 60px;
  font-size: 20px;
  text-transform: uppercase;

  @media screen and (max-width: 767px) {
    margin-bottom: 9.583vw;
  }
}

.section__subtitle {
  flex: 1 1;
  font-size: 60px;
  line-height: 80px;
  text-transform: uppercase;

  .hidden {
    display: none;
  }

  @media screen and (max-width: 767px) {
    font-size: 7.292vw;
    line-height: 10.417vw;

    .hidden {
      display: block;
    }
  }
}
.section__desc {
  flex: 1 1;
  padding: 20px 0;
  font-size: 20px;

  @media screen and (max-width: 767px) {
    font-size: 4.167vw;
  }
}

.section__content {
  width: 100%;
  max-width: 1620px;
  margin: 0 auto;
  padding: 0 30px;

  @media screen and (max-width: 767px) {
    padding-right: 1rem;
    padding-left: 1rem;
    margin-top: 30px;
  }
}

.about__list {
  display: flex;
  gap: 20px;

  @media screen and (max-width: 1440px) {
    gap: 13px;
    margin: 0 -1rem;
    padding: 8px 1rem;
    white-space: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-scroll-snap-type: x mandatory;
    scroll-snap-type: x mandatory;
    -webkit-scroll-padding-left: 1rem;
    scroll-padding-left: 1rem;
  }
}
.about__item {
  position: relative;
  width: calc(100% / 4);
  max-width: 390px;
  height: 520px;
  max-height: 50vh;
  padding: 60px;
  border: 1px solid #000;
  transition: all 0.3s;

  @media screen and (max-width: 1440px) {
    flex: none;
    padding: 4.167vw;
    -webkit-scroll-snap-align: start;
    scroll-snap-align: start;

    @supports (width: calc((1% - 1px) / 1)) {
      width: calc((100% - 136px) / 1);
    }
  }

  @media screen and (max-width: 767px) {
    padding: 6.25vw;
    height: 60.417vw;
  }
  @media (hover: hover) {
    &:hover {
      .number {
        color: rgba(0, 0, 0, 0.15);
      }
      .about__title {
        top: 60px;
        transform: translate(0px);

        @media screen and (max-width: 1440px) {
          top: 4.167vw;
        }
        @media screen and (max-width: 767px) {
          top: 12.5vw;
        }
      }

      .about__desc {
        opacity: 1;
        transition-delay: 0.1s;
        transform: translate(0px);
      }
    }
  }
}

.about__cont {
  .number {
    position: absolute;
    top: -18px;
    right: -9px;
    overflow: hidden;
    font-weight: bold;
    font-size: 200px;
    color: rgba(0, 0, 0, 0.07);
    line-height: 180px;
    transition: color 0.5s cubic-bezier(0.16, 0.68, 0.43, 0.09);

    @media screen and (max-width: 1440px) {
      font-size: 13.889vw;
      line-height: 12.5vw;
    }
    @media screen and (max-width: 767px) {
      top: -12px;
      right: -4px;
      font-size: 20.833vw;
      color: rgba(0, 0, 0, 0.15);
      line-height: 20.833vw;
    }
  }
}

.about__title {
  position: absolute;
  top: calc(100% - 132px);
  font-size: 30px;
  font-weight: 600;
  line-height: 1.2;
  transition: top 0.5s cubic-bezier(0.16, 0.68, 0.43, 0.99), opacity 0.5s ease-in-out;

  @media screen and (max-width: 767px) {
    top: 20px;
    font-size: 4.167vw;
  }
}

.about__desc {
  position: absolute;
  bottom: 60px;
  right: 60px;
  left: 60px;
  opacity: 0;
  font-size: 20px;
  line-height: 1.5;
  transform: translateY(30px);
  transition: transform 0.5s cubic-bezier(0.16, 0.68, 0.43, 0.99), opacity 0.5s ease-in-out;

  @media screen and (max-width: 1440px) {
    white-space: normal;
  }

  @media screen and (max-width: 767px) {
    bottom: 6.25vw;
    right: 6.25vw;
    left: 6.25vw;
    font-size: 2.917vw;
    opacity: 1;
    transform: translate(0px);
  }
}

.business__content {
  max-width: 1340px;
  margin-top: 100px;
}

.business__list {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;

  @media screen and (max-width: 1365px) {
    flex-direction: column;
  }
}

.business__item {
  flex: 1 1;
  position: relative;
  margin: 0 40px;
  font-size: 18px;
  line-height: 1.33;
  text-align: left;

  dt {
    margin-bottom: 40px;
    font-size: 30px;
    font-weight: 600;

    &::after {
      content: '';
      display: block;
      height: 200px;
      margin-top: 40px;
      background-position: center center;
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  dd {
    font-size: 20px;
    line-height: 1.6;

    & + dd {
      margin-top: 1rem;
    }
  }

  &:nth-child(1) dt::after {
    background-image: url('../images/about/about01.png');
    border-radius: 100px;
  }
  &:nth-child(2) dt::after {
    background-image: url('../images/about/about02.png');
    border-radius: 100px 0;
  }
  &:nth-child(3) dt::after {
    background-image: url('../images/about/about03.png');
    border-radius: 0 100px 100px 0;
  }
}

@media screen and (min-width: 960px) and (max-width: 1365px) {
  .business__item {
    min-height: 200px;
    padding-left: 400px;
    margin-bottom: 50px;

    dt {
      &::after {
        position: absolute;
        top: 0;
        left: 0;
        width: 360px;
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 959px) {
  .business__item {
    margin: 0;
    margin-bottom: 20px;

    dt {
      margin-bottom: 20px;
      font-size: 20px;

      &::after {
        height: 160px;
        margin-top: 20px;
      }
    }

    dd {
      font-size: 13px;

      & + dd {
        margin-top: 0.5rem;
      }
    }
  }
}

.marketing {
  display: flex;
  flex-direction: column;
  padding-bottom: 0;
}

.marketing__content {
  flex: 1 1;
}

.marketing__section {
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding-bottom: 75px;
}

.marketing__area {
  display: flex;
  flex-direction: column;

  h4 {
    margin-bottom: 0.5rem;
    font-size: 13px;
    color: rgba(0, 0, 0, 0.5);
  }

  ul li {
    font-weight: 600;
    font-size: 18px;
    color: #111;
  }
}

.marketing__text {
  position: absolute;
  bottom: 0;
  left: 0;
}

.marketing__word {
  font-weight: 900;
  font-size: 50px;
  color: rgba(0, 0, 0, 0.04);
  line-height: 60px;
  letter-spacing: -0.5px;
  text-transform: uppercase;
}

@media screen and (min-width: 960px) {
  .marketing__section {
    max-width: 1320px;
    padding: 0 30px;
  }

  .marketing__area {
    flex-direction: row;
    justify-content: space-between;
    min-width: 660px;
    margin-left: auto;

    h4 {
      font-size: 20px;
    }

    ul {
      min-width: 420px;

      li {
        font-size: 26px;
      }
    }
  }

  .marketing__word {
    font-size: 200px;
    line-height: 160px;
  }
}

/* WORK */
.work__content {
  margin-top: 50px;
}

.client__list {
  display: flex;
  flex-wrap: wrap;
}

.client__item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 20%;
  height: 0;
  padding-bottom: 10%;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: auto;
    height: auto;
    padding: 20px;
    transform: translate(-50%, -50%);
  }

  @media screen and (min-width: 1200px) {
    &:nth-child(n + 6):nth-child(-n + 9) {
      width: 25%;
    }
  }

  @media screen and (max-width: 1199px) {
    width: calc(100% / 3);
    margin-bottom: 2%;
  }

  @media screen and (max-width: 767px) {
    width: calc(100% / 2);
    padding-bottom: 20%;

    img {
      padding: 0 30px;
    }
  }

  img {
    filter: grayscale(1);
  }
}

/* LOCATION */
.location__item {
  display: flex;
  flex-direction: column;

  > span {
    display: flex;
  }

  & + .location__item {
    margin-top: 70px;

    @media screen and (max-width: 959px) {
      margin-top: 30px;
    }
  }
}

.location__title {
  font-size: 20px;
  color: #777;
  opacity: 0;
  -webkit-animation: text-up 1s both;
  animation: text-up 1s both;

  @media screen and (max-width: 959px) {
    font-size: 13px;
  }
}

.location__address,
.location__email a {
  font-weight: 700;
  font-size: 30px;
  color: #111;
  opacity: 0;
  -webkit-animation: text-up 1.5s both;
  animation: text-up 1.5s both;

  @media screen and (max-width: 959px) {
    font-size: 18px;
  }
}

.map__content {
  margin-top: 40px;

  @media screen and (min-width: 960px) {
    margin-top: 70px;
  }
}

.map {
  position: relative;
  width: 100%;
  min-height: 320px;
  background: url(../images/map.png) no-repeat 50%;
  filter: grayscale(1);
}

@-webkit-keyframes text-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(5rem);
    transform: translateY(5rem);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0rem);
    transform: translateY(0rem);
  }
}

@keyframes text-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(5rem);
    transform: translateY(5rem);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0rem);
    transform: translateY(0rem);
  }
}
